.kirovets_tabs{
	background:#18213D linear-gradient(275.65deg, rgba(24, 33, 61, 0.7) 31.58%, rgba(24, 33, 61, 0.25028) 69.08%, rgba(24, 33, 61, 0.5) 88.25%);
	background-blend-mode: multiply;
	padding-top: 40px;
	padding-bottom: 60px;
}

.kirovets_tabs__title{
	@extend %block-title;
	color: #ffffff;
	width: 90%;
}

.kirovets_tabs__nav{
	border-bottom: 1px solid #535765;
	margin-top: 25px;
}

.kirovets_tabs__list{
	display: flex;
	justify-content: space-between;
	@include list-destroy;
}
.kirovets_tabs__nav-btn{
	@include btn-destroy;
	font-family: $font-bold-title;
	font-style: normal;
	font-weight: 900;
	font-size: 16px;
	line-height: 17px;
	text-transform: uppercase;

	color: #FFFFFF;

	opacity: 0.29;

	// margin-right: 20px;
	padding-bottom: 10px;
	position: relative;
}

.kirovets_tabs__nav-btn.active{
	color: #C26063;
	opacity: 1;
}
.kirovets_tabs__nav-btn.active::before{
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: #C26063;
	bottom: -1px;
	left:0;
}
.kirovets_tabs__slider{
	@include list-destroy;
	width: 100%;
}
.kirovets_tabs__slide{
	width: 100%;
}


.kirovets_tabs__slide{
	padding-top: 320px;
	background-image: url(../img/model-k7m.png);
	background-position: center 30px;
	background-size: 100%;
	background-repeat: no-repeat;
}
.kirovets_tabs__features-list{
	@include list-destroy;
	padding-bottom: 20px;
}
.kirovets_tabs__features-subtitle{
	margin: 0;
	padding: 0;
	font-style: normal;
	font-weight: normal;
	font-family: $font-regular;
	font-size: 18px;
	line-height: 22px;
	text-transform: uppercase;

	color: rgba(255, 255, 255, 0.69);
}

.kirovets_tabs__features-desc{
	@extend %block-text;
	color: rgba(255, 255, 255, 0.5);
	margin-top: 10px;
	width: 90%;
}
.kirovets_tabs__features-item{
	padding-bottom: 5px;
}

.swiper-container-horizontal > .kirovets_swiper-scrollbar--tabs.swiper-scrollbar {
	background-color: rgba(255, 255, 255, 0.25);
}
.kirovets_swiper-scrollbar--tabs .swiper-scrollbar-drag{
	height: 6px;


}
